                                <p>13.7 Upon BankMudraa serving a notice of termination, or upon expiry of the Term, the Retailer/Distributor/Super Distributor shall ensure that during the period of 1 (one) month leading to the termination, all systems and procedures will be strictly adhered to and all customers are handled properly. All enquiries from customers will be diverted to BankMudraa.</p>
