import { useState, useEffect } from "react";
import MerchantLoginForm from "./merchantLoginPhone/MerchantLoginPhone";
import MerchantResetPassword from "./merchentResetPassword/MerchantResetPassword";
import { isAndroid, isIOS, isDesktop } from "react-device-detect";
import usePWAInstall from "../../hooks/usePWAInstall";
import Slider from "react-slick";
import {
  EmailGenerateOtp,
  emailGenerateOtp,
  isEmailExits,
  isMobileExits,
  mobileGenerateOtp,
} from "../../api/login/Login";
import { toast } from "react-toastify";
import useDebouncedValue from "../../utils/useDebouncedValue";
import tranParentLogo from "../../asesets/banner/transparentLogo.png";
import bankmudraa_logo from "../../asesets/banner/bankmudraa_logonew.png";
import Footer from "../footer/footer";
import AnimatedWrapper from "../../common/AnimateWrapper";
import carousel1 from "../../asesets/banner/login/1.png";
import carousel2 from "../../asesets/banner/login/2.png";
import carousel3 from "../../asesets/banner/login/3.png";
import carousel4 from "../../asesets/banner/login/4.png";

function MerchantLoginArea({ tokenNoti }) {
  useEffect(() => {
    const root = document.documentElement;

    root.style.setProperty("--bs-border-primary1", "#FFFF");
    root.style.setProperty("--bs-primary3", "rgb(23, 59, 75)");
    root.style.setProperty("--bs-primary1", "#23627e");
    root.style.setProperty("--bs-primary2", "#f8fafc");
    root.style.setProperty("--bs-secondary1", "#12599c");
    root.style.setProperty("--bs-secondary2", "rgb(23, 59, 75)");
    root.style.setProperty("--bs-nav", "#1e293b");
  }, []);
  const [loginForm, setLoginForm] = useState(true);
  const [initalValue, setInitialValue] = useState({
    mobileNo: "+91",
    emailId: "",
    pan: "",
  });
  const [userId, setUserID] = useState(null);
  const [buttonDisable, setButtonDisable] = useState(false);
  const [mobileMsg, setMobileMsg] = useState("");
  const [loader, setLoader] = useState(false);
  const [loader3, setloader3] = useState(false);
  const [resetOtp, setResetOtp] = useState(true);
  const [OtpUp, setOtpUp] = useState(false);
  const [openReset, setOpenReset] = useState(false);
  const [emailError, setemailError] = useState();
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const [EmailpanError, setEmailpanError] = useState();
  const [mobileError, setMobileError] = useState();
  const [panError, setpanError] = useState();
  const [isMobileExist, setIsMobileExist] = useState(null);
  const handleSubmitChange = () => setLoginForm(false);
  const handleSubmitBack = () => setLoginForm(true);
  const debouncedMobile = useDebouncedValue(initalValue.mobileNo, 600);
  const debouncedEmail = useDebouncedValue(initalValue.emailId, 600);
  const handleChange = (e, isMobile) => {
    const clone = { ...initalValue };
    clone[e.target.name] = e.target.value.replace(/\s/g, "");
    setInitialValue(clone);
    setButtonDisable(false);
    setIsMobileExist?.(null);
  };
  useEffect(() => {
    if (debouncedMobile && debouncedMobile.length === 13) {
      isMobileExit(debouncedMobile);
    }
  }, [debouncedMobile]);
  useEffect(() => {
    if (
      debouncedEmail &&
      debouncedEmail.includes("@") &&
      debouncedEmail.length > 5
    ) {
      isEmailExit(debouncedEmail);
    }
  }, [debouncedEmail]);

  const isMobileExit = async (mobile) => {
    try {
      const res = await isMobileExits(mobile);
      if (!res?.data?.isExist) {
        setMobileMsg("Mobile Number Does Not Exist");
        setButtonDisable(true);
        setIsMobileExist(false);
      } else {
        setMobileMsg("");
        setButtonDisable(false);
        setIsMobileExist(true); 
      }
    } catch (err) {}
  };

  const isEmailExit = async (email) => {
    try {
      const res = await isEmailExits(email);
      if (!res?.data?.isExist) {
        setMobileMsg("Email Does Not Exist");
        setButtonDisable(true);
      } else {
        setMobileMsg("");
        setButtonDisable(false);
      }
    } catch (err) {}
  };

  const toastSuccessMessage = (msg) => {
    toast.success(msg || "OTP Sent Successfully", {
      position: "top-center",
      autoClose: 2000,
    });
  };

  const generateOtp = async (useEmail = false) => {
    setLoader(true);
    const payload = useEmail
      ? { emailId: initalValue.emailId, pan: initalValue.pan }
      : { mobileNo: initalValue.mobileNo, pan: initalValue.pan };

    try {
      const res = useEmail
        ? await emailGenerateOtp(payload)
        : await mobileGenerateOtp(payload);
      setUserID(res?.data?.data?.user);
      window.localStorage.setItem("forgotUser", res?.data?.data?.user);

      if (res?.data?.statusCode === 200) {
        toastSuccessMessage();
        setResetOtp(false);
        setOtpUp(true);
        setOpenReset(true);
      }

      if (res?.data?.statusCode === 401 || res?.data?.statusCode === 402) {
        setMobileMsg(res?.data?.message || "Invalid Mobile/Email or PAN");
      }
    } catch (err) {}
    setLoader(false);
  };

  const sliderSettings = {
    dots: true,
    infinite: true,
    autoplay: true,
    autoplaySpeed: 3500,
    speed: 1000,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    fade: true,
  };

  const mobileGenerateOtpMobile = async (radioSet) => {
    const cloneMobile = {
      mobileNo: initalValue?.mobileNo,
      pan: initalValue?.pan,
    };
    const cloneMail = { emailId: initalValue?.emailId, pan: initalValue?.pan };

    if (radioSet) {
      setloader3(true);
      try {
        const res2 = await emailGenerateOtp(cloneMail);
        setUserID(res2?.data?.data?.user);
        window.localStorage.setItem("forgotUser", res2?.data?.data?.user);
        // console.log(res2.data.statusCode);
        if (res2?.data?.statusCode == 200) {
          toastSuccessMessage();
          handleShow();
          setTimeout(() => {
            // setResetOtp(false)
            // setOtpUp(true)
          }, 2000);
        }

        if (!cloneMail.emailId) {
          setemailError("Email is  Required!");
          // setpanError('Pan Number no is wrong')
        } else {
          setemailError("");
        }
        if (!cloneMail.pan) {
          setEmailpanError("Pan Number is Required!");
          // setMobileError(' mobile no is Required !')
        } else {
          setEmailpanError("");
        }

        if (res2?.data?.statusCode == 401) {
          setemailError("Email is wrong");
          setEmailpanError("Pan Number is wrong");
        }
        if (res2?.data?.statusCode == "402") {
          setEmailpanError("Pan Number is Worng!");
          // setemailError('')
        }
        setloader3(false);
      } catch (error) {
        setloader3(false);
      }
    } else {
      setloader3(true);
      try {
        const res = await mobileGenerateOtp(cloneMobile);

        setUserID(res?.data?.data?.user);

        window.localStorage.setItem("forgotUser", res?.data?.data?.user);
        // console.log(res.data.statusCode);
        if (res?.data?.statusCode == 200) {
          toastSuccessMessage();
          handleShow();
          setTimeout(() => {
            // setResetOtp(false)
            // setOtpUp(true)
          }, 2000);
        }

        if (cloneMobile.mobileNo?.length < 13) {
          setMobileError("mobile Number is Required !");
          // setpanError('Pan Number no is wrong')
        } else {
          setMobileError("");
        }
        if (!cloneMobile.pan) {
          setpanError("Pan Number  is Required !");
          // setMobileError(' mobile no is Required !')
        } else {
          setpanError("");
        }

        if (res?.data?.statusCode == 401) {
          setMobileError("Mobile number is wrong");
          setpanError("Pan Number is wrong");
        }
        if (res?.data?.statusCode == "402") {
          setpanError("Pan Number is wrong");
        }
        setloader3(false);
      } catch (error) {
        setloader3(false);
      }
    }
  };

  const getOtp = (radioSet) => {
    // console.log("mustafa");
    // if (radioSet) {
    //     isEmailExitt()
    // } else {
    //     mobileGenerateOtpMobile()
    // }

    mobileGenerateOtpMobile(radioSet);
  };

  const carouselImages = [carousel4, carousel1, carousel2, carousel3];

 const AppDownloadBanner = () => {
  const { canInstall, installApp, isInstalled } = usePWAInstall();

  if (isInstalled) return null;

  return (
    <div style={{ textAlign: "center", marginBottom: "10px" }}>
      {(isAndroid || isDesktop) && canInstall && (
        <button
          onClick={installApp}
          style={{
            width: "60%",
            padding: "8px 10px",
            background: "#22c55e",
            color: "#fff",
            borderRadius: "6px",
            fontSize: "12.5px",
            border: "none",
            cursor: "pointer",
            fontWeight: 600,
            margin: "0 auto",
            display: "block",
          }}
        >
          ⬇ Install BankMudraa App
        </button>
      )}

      {/* iOS manual install */}
      {isIOS && (
        <div
          style={{
            marginTop: "8px",
            padding: "8px",
            fontSize: "13px",
            color: "#e5e7eb",
            border: "1px dashed rgba(255,255,255,0.4)",
            borderRadius: "6px",
          }}
        >
          📱 Install App:
          <br />
          Tap <strong>Share</strong> → <strong>Add to Home Screen</strong>
        </div>
      )}
    </div>
  );
};

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100dvh",
        fontFamily: "Inter, sans-serif",
      }}
    >
      <div
        style={{
          display: "flex",
          flex: 1,
          flexDirection: "row",
          background: "linear-gradient(135deg, #012a60 50%, #012a60 50%)",
          flexWrap: "wrap",
        }}
      >
        {/* LEFT INFO SECTION */}

        <div
          className="d-none d-md-flex"
          style={{
            flex: 1,
            minWidth: "300px",
            position: "relative",
            display: window.innerWidth < 768 ? "none" : "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
            overflow: "hidden",
            background: "linear-gradient(135deg, #012a60, #012a60",
            color: "#fff",
          }}
        >
          {/* <div
            style={{
              position: "absolute",
              top: 30,
              left: 30,
              zIndex: 10,
              display: "flex",
              alignItems: "center",
              gap: "10px",
            }}
          >
            <img
              src={tranParentLogo}
              alt="Logo"
              style={{ width: "80px", height: "80px" }}
            />
            <h1 style={{ fontSize: "24px", fontWeight: "bold" }}>
              Merchant Portal
            </h1>
          </div> */}

          <Slider {...sliderSettings} style={{ width: "100%", height: "100%" }}>
            {carouselImages.map((img, i) => (
              <div key={i}>
                <img
                  src={img}
                  alt={`Slide ${i}`}
                  style={{
                    width: "100%",
                    height: "95vh",
                    objectFit: "cover",
                    // filter: "brightness(0.7)",
                  }}
                />
                {/* <div
                  style={{
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    transform: "translate(-50%, -50%)",
                    textAlign: "center",
                    color: "#fff",
                  }}
                >
                  <h2 style={{ fontSize: "28px", fontWeight: "600" }}>
                    Fast & Secure Payments
                  </h2>
                  <p style={{ fontSize: "16px", marginTop: "8px" }}>
                    Experience seamless merchant onboarding and payouts.
                  </p>
                </div> */}
              </div>
            ))}
          </Slider>
        </div>

        {/* RIGHT LOGIN SECTION */}
        <div
          className="login-right-container"
          style={{
            flex: 1,
            minWidth: "300px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            background: "#012a60",
            color: "#fff",
            // padding: "40px",
          }}
        >
          {/* <div
            style={{
              position: "absolute",
              top: 30,
              left: 30,
              zIndex: 10,
              display: "flex",
              alignItems: "center",
              gap: "10px",
            }}
          >
            <img
              src={tranParentLogo}
              alt="Logo"
              style={{ width: "80px", height: "80px" }}
            />
            <h1 style={{ fontSize: "24px", fontWeight: "bold" }}>
              Merchant Portal
            </h1>
          </div> */}

          <div
            className="login-card"
            style={{
              backgroundColor: "rgba(255,255,255,0.1)",
              backdropFilter: "blur(10px)",
              borderRadius: "20px",
              width: "100%",
              maxWidth: "400px",
              padding: "25px 20px",
              boxShadow: "0 8px 24px rgba(0,0,0,0.2)",
            }}
          >
            {/* <div
            style={{
              position: "absolute",
              top: 30,
              left: 30,
              zIndex: 10,
              display: "flex",
              alignItems: "center",
              gap: "10px",
            }}
          >
            <img
              src={tranParentLogo}
              alt="Logo"
              style={{ width: "80px", height: "80px" }}
            />
            <h1 style={{ fontSize: "24px", fontWeight: "bold" }}>
              Merchant Portal
            </h1>
          </div> */}

            <AnimatedWrapper key={loginForm}>
              <div
                className="login-logo-wrapper d-md-none"
                style={{ textAlign: "center", marginBottom: "8px" }}
              >
                <img
                  src={bankmudraa_logo}
                  alt="BankMudraa Logo"
                  className="login-mobile-logo"
                />
              </div>
              <h2
                className="login-title"
                style={{
                  textAlign: "center",
                  marginBottom: "6px",
                  color: "#fff",
                  fontSize: "24px",
                  letterSpacing: "1px",
                }}
              >
                {loginForm ? "LOGIN" : "Forgot Password"}
              </h2>
              <AppDownloadBanner />
              <p className="login-subtitle-text">
                {loginForm
                  ? "Secure access to your dashboard."
                  : "Reset your password using your registered details."}
              </p>

              {/* Login Form with +91 prefix */}
              {loginForm ? (
                <div>
                  <MerchantLoginForm handleSubmitChange={handleSubmitChange} />
                </div>
              ) : (
                <div>
                  <MerchantResetPassword
                    handleSubmitBack={handleSubmitBack}
                    resetOtp={resetOtp}
                    getOtp={getOtp}
                    OtpUp={OtpUp}
                    openReset={openReset}
                    handleChange={handleChange}
                    initalValue={initalValue}
                    userId={userId}
                    setOtpUp={setOtpUp}
                    setOpenReset={setOpenReset}
                    setLoginForm={setLoginForm}
                    setInitialValue={setInitialValue}
                    buttonDisable={buttonDisable}
                    mobileMsg={mobileMsg}
                    setButtonDisable={setButtonDisable}
                    setMobileMsg={setMobileMsg}
                    mobileError={mobileError}
                    panError={panError}
                    emailError={emailError}
                    EmailpanError={EmailpanError}
                    loader3={loader3}
                    show={show}
                    handleClose={handleClose}
                    setResetOtp={setResetOtp}
                    isMobileExist={isMobileExist}
                  />
                </div>
              )}
            </AnimatedWrapper>
          </div>
        </div>
      </div>

      {/* FOOTER FIXED */}
      <div style={{ backgroundColor: "#012a60", color: "#fff" }}>
        <Footer notIsFixed={true} />
      </div>
    </div>
  );
}

export default MerchantLoginArea;
