import { useRef, useState, useEffect } from "react";
import { MdOutlinePhoneAndroid } from "react-icons/md";
import { RiLockPasswordFill } from "react-icons/ri";
import { userLogin, userLoginOtp } from "../../../api/login/Login";
import {
  SaveUserDeatilsLocalStorage,
  getUserTpinStatus,
  setUserType,
} from "../../../utils/localStorage";
import { resendOtpsloginForm } from "../../../api/login/Login";
import { useNavigate } from "react-router";
import { IoMdEye } from "react-icons/io";
import { FaEyeSlash } from "react-icons/fa";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";
// import { isValidNumber } from 'libphonenumber-js';
import TermAndCondition from "./termAndCondition/TermAndCondition";
import { MdOutlineEmail } from "react-icons/md";
import VerifyOtp from "./verifyotp/VerifyOtp";
function MerchantLoginForm({ handleSubmitChange }) {
  const [loader1, setloader2] = useState(false);
  const [PassWordError, setPassWordError] = useState();
  // console.log(PassWordError);
  const [isChecked, setIsChecked] = useState(false);
  const [isFormSubmitted, setIsFormSubmitted] = useState(false);
  const [show, setShow] = useState(false);
  const [show2, setShow2] = useState(false);
  const [logintoken, setLoginToken] = useState("");
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const handleClose2 = () => setShow2(false);
  const handleShow2 = () => setShow2(true);
  const [initialValue, setInitialValue] = useState(() => {
    try {
      const savedCredentials =
        localStorage.getItem("rememberedCredentials") ||
        sessionStorage.getItem("rememberedCredentials") ||
        localStorage.getItem("user_remember_me");

      if (savedCredentials) {
        const parsed = JSON.parse(savedCredentials);
        if (parsed && parsed.entity) {
          return {
            entity: parsed.entity,
            password: parsed.password || "",
          };
        }
      }
    } catch (error) {
      console.error("Error initializing credentials:", error);
    }
    return {
      entity: "+91",
      password: "",
    };
  });
  const [radioSet, setRadioSet] = useState(false);
  const [rememberMe, setRememberMe] = useState(false);
  const [errorValue, setErrorValue] = useState({});
  const [isFormValid, setIsFormValid] = useState(false);
  const [storedMobileNo, setStoredMobileNo] = useState("");
  const [closeotpmodal, handleClseOtpModal] = useState(false);
  useEffect(() => {
    try {
      let savedCredentials = localStorage.getItem("rememberedCredentials");
      if (!savedCredentials) {
        savedCredentials = sessionStorage.getItem("rememberedCredentials");
        if (savedCredentials) {
          localStorage.setItem("rememberedCredentials", savedCredentials);
        }
      }
      if (!savedCredentials) {
        savedCredentials = localStorage.getItem("user_remember_me");
        if (savedCredentials) {
          localStorage.setItem("rememberedCredentials", savedCredentials);
        }
      }
      // console.log("Loading saved credentials:", savedCredentials);
      if (savedCredentials) {
        const parsed = JSON.parse(savedCredentials);
        // console.log("Parsed credentials:", parsed);
        if (parsed && parsed.entity) {
          setInitialValue((prev) => ({
            ...prev,
            entity: parsed.entity,
          }));
          setRememberMe(true);
          setRadioSet(parsed.isEmail || false);
          setErrorValue({});
        }
      }
    } catch (error) {
      console.error("Error loading saved credentials:", error);
      localStorage.removeItem("rememberedCredentials");
      sessionStorage.removeItem("rememberedCredentials");
    }
  }, []);
  const toastSuccessMessage = (str) => {
    toast.success(str);
  };
  const toastSuccessMessageError = (str) => {
    toast.error(str);
  };
  const validation = (values) => {
    const error = {};
    const email_pattern = /^[^\s@]+@[^\s@]+\.[^\s@]{2,6}$/;
    if (!radioSet) {
      const splits2 = values.entity.length;
      // const checkn = isValidNumber(values.entity)
      // console.log(checkn);
      if (splits2 < 13) {
        error.entity = "Mobile Number should be of 10 digit!";
      } else if (values.entity == "+91") {
        error.entity = "Mobile Number should be of 10 digit!";
      }
    } else {
      if (!values.entity) {
        error.entity = "Email is required !";
      } else if (!email_pattern.test(values.entity)) {
        error.entity = "Email is not correct";
      }
    }
    // else if (!regexEmail.test(values.email)) {
    //     error.email = "Invalid Email";
    // }
    if (!values.password) {
      error.password = "Password is Required!";
    }
    return error;
  };
  const checkFormValidity = (errors, values) => {
    const isValid =
      Object.keys(errors).length === 0 &&
      values.entity &&
      values.entity !== "+91" &&
      values.password &&
      (radioSet ? values.entity.includes("@") : values.entity.length === 13);
    setIsFormValid(isValid);
  };
  const handleUser = (e, str) => {
    if (!isChecked) {
      setIsFormSubmitted(true);
    }
    if (str) {
      const clone = { ...initialValue };
      const name = e.target.name;

      let value = e.target.value.replace(/[^0-9+]/g, "");

      if (!value.startsWith("+91")) {
        value = "+91";
      }

      if (value.length > 13) {
        value = value.slice(0, 13);
      }

      clone[name] = value;

      setErrorValue(validation(clone));
      setInitialValue(clone);
      return;
    }

    const clone = { ...initialValue };
    const value = e.target.value;
    const name = e.target.name;
    clone[name] = value;
    const newErrors = validation(clone);
    setErrorValue(newErrors);
    setInitialValue(clone);
    checkFormValidity(newErrors, clone);
  };

  const handlePaste = (event) => {
    event.preventDefault();
  };
  const inputRef = useRef(null);
  const handleMouseDown = (event) => {
    if (document.activeElement === inputRef.current) {
      event.preventDefault();
    }
  };
  const handleSelect = (event) => {
    const input = inputRef.current;
    if (input) {
      const start = input.selectionStart;
      const end = input.selectionEnd;
      if (start < 3) {
        event.preventDefault();
        if (end > 3) {
          input.setSelectionRange(3, end);
        } else {
          input.setSelectionRange(3, 3);
        }
      }
    }
  };
  const navigate = useNavigate();
  const [clicked, setclicked] = useState(false);
  const loginOtp = async (val) => {
    const obj = {
      user_id: window.localStorage.getItem("userIdToken"),
      otp: val.join(""),
    };
    try {
      const res = await userLoginOtp(obj);
      if (res.statusCode == 200) {
        const rememberedCreds =
          localStorage.getItem("rememberedCredentials") ||
          sessionStorage.getItem("rememberedCredentials") ||
          localStorage.getItem("user_remember_me");
        SaveUserDeatilsLocalStorage(res?.data?.user);
        getUserTpinStatus(res?.data?.Tpin_status);
        setUserType("userType", res.data.user_type_name);
        window.localStorage.setItem("regisNumber", res.data?.mobile);
        if (rememberedCreds) {
          localStorage.setItem("rememberedCredentials", rememberedCreds);
          sessionStorage.setItem("rememberedCredentials", rememberedCreds);
          localStorage.setItem("user_remember_me", rememberedCreds);
          // console.log("Credentials preserved and restored after login");
        }
        toastSuccessMessage(res?.data?.message || "Login Successfully");
        setTimeout(() => {
          if (
            res?.data?.is_approved === true &&
            res?.data?.is_self_declare === true
          ) {
            navigate("/Dashboard");
          } else {
            navigate("/registrationComplete");
          }
          window.location.reload();
        }, 1000);
        handleClose2();
      } else if (res.statusCode == 403) {
        toastSuccessMessageError(res?.message || "Otp not matched");
      } else {
        toastSuccessMessageError(res.message || "An error occurred");
      }
    } catch (error) {
      console.error("Error in loginOtp:", error);
      toastSuccessMessageError("An unexpected error occurred");
    }
  };

  const handleSubmit = async (event) => {
    // console.log(initialValue);
    setloader2(true);
    event.preventDefault();
    setclicked(true);
    setErrorValue(validation(initialValue));

    setIsFormSubmitted(true);
    if (!radioSet) {
      setStoredMobileNo(initialValue.entity);
    }
    if (rememberMe) {
      const credentialsToSave = {
        entity: initialValue.entity,
        isEmail: radioSet,
        password: initialValue.password,
      };
      localStorage.setItem(
        "rememberedCredentials",
        JSON.stringify(credentialsToSave)
      );
      sessionStorage.setItem(
        "rememberedCredentials",
        JSON.stringify(credentialsToSave)
      );
      localStorage.setItem(
        "user_remember_me",
        JSON.stringify(credentialsToSave)
      );

      // console.log(
      //   "Credentials saved on submit (no password):",
      //   credentialsToSave
      // );
    } else {
      localStorage.removeItem("rememberedCredentials");
      sessionStorage.removeItem("rememberedCredentials");
      localStorage.removeItem("user_remember_me");
      // console.log("Credentials cleared on submit");
    }
    try {
      const res = await userLogin(initialValue);
      SaveUserDeatilsLocalStorage(res?.data?.user);

      // console.log("logininfo", res);
      window.localStorage.setItem("openMenu", false);

      if (res.statusCode == 200) {
        setLoginToken(res?.data?.user);
        // console.log("loginToken", res?.data?.user); // Log the token directly
        toastSuccessMessage("Otp Send Successfully");

        // toastSuccessMessage('Login Successfully')

        // navigate('/registrationComplete')
        setTimeout(() => {
          handleShow2();
          // window.location.reload()
        }, 1000);
      }
      if (res.statusCode === 402) {
        toastSuccessMessageError(res.message);
      }
      if (res.statusCode == 406) {
        toastSuccessMessageError(res.message);
        setPassWordError(res.message);
      }
      if (res.statusCode == 405) {
        toastSuccessMessageError(res.message);
      }
      setloader2(false);
    } catch (error) {
      toastSuccessMessageError("Something went Wrong.");
      setloader2(false);
    }
    setclicked(false);
  };
  const [showPassword, setShowPassword] = useState(false);
  const toggleShowPassword = () => {
    // console.log('fgfd', !showPassword);
    setShowPassword(!showPassword);
  };

  const handleCheckboxChange = (e) => {
    setIsChecked(e.target.checked);
  };

  const isValidEmail = (email) => {
    const email_pattern = /^[^\s@]+@[^\s@]+\.[^\s@]{2,6}$/;
    return email_pattern.test(email);
  };

  const isValidMobile = (mobile) => {
    return mobile.length === 13;
  };

  const isValidPassword = (password) => {
    return password.length !== 0;
  };

  const isFormValid2 = () => {
    if (radioSet) {
      return (
        isValidEmail(initialValue.entity) &&
        isValidPassword(initialValue.password) &&
        isChecked
      );
    } else {
      return (
        isValidMobile(initialValue.entity) &&
        isValidPassword(initialValue.password) &&
        isChecked
      );
    }
  };
  return (
    <>
      <div className="mobile-login-phone">
        <form action="" onSubmit={handleSubmit}>
          {radioSet ? (
            <>
              {" "}
              <div className="input-group  mb-2">
                <span className="input-group-text" id="basic-addon1">
                  <MdOutlineEmail />
                </span>
                <input
                  type="mail"
                  className={`normal-input form-control ${
                    initialValue.entity && isValidEmail(initialValue.entity)
                      ? "is-valid"
                      : ""
                  }`}
                  placeholder="Enter Email"
                  name="entity"
                  value={initialValue?.entity}
                  onChange={handleUser}
                  autoComplete="off"
                />
              </div>
              <div id="emailHelp" className="form-text text-danger">
                {errorValue.entity}
              </div>
            </>
          ) : (
            <>
              <div className="input-group  mb-2">
                <span className="input-group-text" id="basic-addon1">
                  <MdOutlinePhoneAndroid />
                </span>
                <input
                  type="tel"
                  inputMode="numeric"
                  pattern="\+?[0-9]*"
                  placeholder="Enter Mobile Number"
                  className={`normal-input form-control ${
                    initialValue.entity && isValidMobile(initialValue.entity)
                      ? "is-valid"
                      : ""
                  }`}
                  ref={inputRef}
                  onMouseDown={handleMouseDown}
                  onSelect={handleSelect}
                  onPaste={handlePaste}
                  name="entity"
                  value={initialValue?.entity}
                  onChange={(e) => {
                    handleUser(e, "mobile");
                  }}
                  autoComplete="off"
                />
              </div>
              <div id="emailHelp" className="form-text text-danger">
                {errorValue.entity}
              </div>
            </>
          )}
          <div className="input-group input-group-eye mb-2">
            <span className="input-group-text" id="basic-addon1">
              <RiLockPasswordFill />
            </span>
            <input
              type={showPassword ? "text" : "password"}
              style={{
                borderTopRightRadius: "7px",
                borderBottomRightRadius: "7px",
              }}
              className={`form-control ${
                isValidPassword(initialValue.password) ? "is-valid" : ""
              }`}
              placeholder="Enter password"
              name="password"
              value={initialValue?.password}
              onChange={handleUser}
            />
            <div
              className="eye mr-4"
              onClick={toggleShowPassword}
              style={{ color: "#9b9a9a" }}
            >
              {showPassword ? <IoMdEye /> : <FaEyeSlash />}{" "}
            </div>
          </div>
          <div id="emailHelp" className="form-text text-danger">
            {PassWordError ? PassWordError : errorValue.password}
          </div>
          {/* <div id="emailHelp" className="form-text ">{ }</div> */}

          <div className="term-condition mb-2">
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                id="Remember"
                checked={rememberMe}
                onChange={(e) => setRememberMe(e.target.checked)}
              />
              <label className="form-check-label" htmlFor="Remember">
                Remember me
              </label>
            </div>
          </div>

          <div className="term-condition mb-2">
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                id="flexCheckDefault"
                onChange={handleCheckboxChange}
                checked={isChecked} // (optional: proper React prop)
              />
              <label className="form-check-label" htmlFor="flexCheckDefault">
                I have read and accepted
                <Link to="/terms-condition">
                  <span> Terms and Conditions</span>
                </Link>
              </label>
            </div>
          </div>
          {isFormSubmitted && !isChecked && (
            <p style={{ color: "red" }}>
              Please accept the terms and conditions.
            </p>
          )}
          <div>
            <button
              type="submit"
              disabled={!isFormValid2() || loader1}
              className={`btn ${
                !isFormValid2() ? "disableebuttoncls not-allowed" : "btn-login"
              }`}
            >
              LOGIN
              {loader1 && (
                <div
                  style={{ height: "16px", width: "16px" }}
                  className="spinner-border"
                  role="status"
                >
                  <span className="visually-hidden">Loading...</span>
                </div>
              )}
            </button>
          </div>
        </form>
        <div className="text-align-center mt-1">
          <div onClick={handleSubmitChange} style={{ cursor: "pointer" }}>
            Forgot your password?
          </div>
          <div>
            {" "}
            Do not have an account ?
            <Link
              to="/Signup"
              className="text-decoration-underline"
              style={{ color: "var(--bs-nav)" }}
            >
              {" "}
              Register
            </Link>{" "}
          </div>
        </div>
      </div>

      <TermAndCondition handleClose={handleClose} show={show} />
      <VerifyOtp
        handleClose={handleClose2}
        show={show2}
        initialValue={initialValue}
        loginOtp={loginOtp}
        logintoken={logintoken}
        storedMobileNo={storedMobileNo}
      />
    </>
  );
}
export default MerchantLoginForm;
